.main-nav {
  width: 320px;
  margin: 0 auto;
}
.main-nav__logo-desktop {
  display: none;
}
.main-nav__logo-tablet {
  display: none;
}
.main-nav__logo-mobile {
  width: 75px;
  height: 22px;
}
.main-nav__logo-svg:focus path,
.main-nav__logo-svg:active path {
  opacity: 0.3;
}
.main-nav__logo-svg:hover path {
  fill: #d22856;
}
.main-nav__wrapper {
  position: relative;
  background-color: #283645;
  width: 320px;
  margin: 0 auto;
}
.main-nav__logo-block {
  position: relative;
  display: flex;
  justify-content: space-between;
  min-height: 66px;
  background-color: rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
}
.main-nav--opened .main-nav__logo-block {
  box-shadow: inset 0 -1px #000000;
  background-color: #283645;
  min-height: 70px;
}
.main-nav__logo-link {
  display: block;
  padding-left: 20px;
  padding-top: 22px;
  box-sizing: border-box;
}
.main-nav__logo-image {
  width: 75px;
  height: 22px;
}
.main-nav__toggle {
  position: relative;
  display: block;
  width: 90px;
  height: 66px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 21px;
  right: 20px;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: #d22856;
  opacity: 0.3;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 16px;
  width: 30px;
  height: 4px;
  border-radius: 1px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::before,
.main-nav--opened .main-nav__toggle:focus::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #d22856;
  opacity: 0.3;
}
.main-nav--closed .main-nav__list {
  display: none;
}
@media (min-width: 660px) {
  .main-nav {
    width: 660px;
    margin: 0 auto;
  }
  .main-nav__logo-tablet {
    display: block;
    width: 147px;
    height: 40px;
  }
  .main-nav__logo-mobile {
    display: none;
  }
  .main-nav__wrapper {
    width: 660px;
  }
  .main-nav--opened .main-nav__logo-block {
    min-height: 110px;
  }
  .main-nav__logo-block {
    min-height: 110px;
    padding-top: 26px;
  }
  .main-nav__logo-link {
    padding-top: 0;
  }
  .main-nav__logo-image {
    margin-bottom: 13px;
    width: 147px;
    height: 40px;
  }
}
@media (min-width: 960px) {
  .main-nav {
    width: 100%;
    margin: 0 auto;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .main-nav__logo-desktop {
    display: block;
    width: 147px;
    height: 40px;
  }
  .main-nav__logo-tablet {
    display: none;
  }
  .main-nav__wrapper {
    width: 940px;
    padding: 0 10px;
    min-height: 110px;
    background-color: transparent;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__logo-link {
    padding: 0;
    min-height: 0;
  }
  .main-nav--opened .main-nav__logo-block,
  .main-nav--closed .main-nav__logo-block {
    background-color: transparent;
    box-shadow: none;
  }
  .main-nav--opened .main-nav__list,
  .main-nav--closed .main-nav__list {
    width: 621px;
    display: flex;
    justify-content: space-between;
  }
}
.site-list {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #283645;
}
.site-list__link {
  height: 64px;
  box-sizing: border-box;
  color: #ffffff;
  font-size: 18px;
  line-height: 32px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  padding: 16px 0;
  box-shadow: inset 0 1px rgba(255, 255, 255, 0.15), inset 0 -1px #000000;
}
