}
@media (min-width: 960px) {
  .site-list {
    background-color: transparent;
  }
  .site-list__link {
    font-weight: normal;
    text-transform: none;
    box-shadow: none;
    height: 40px;
    padding: 5px 0 0;
  }
  .site-list__item--active {
    border-bottom: 2px solid #ffffff;
  }
  .site-list__link[href]:active {
    color: #ffffff;
  }
}
